import { INTV_META_DATA, CHECKLIST_META_DATA } from '@meddbriefer/scenario-data/traumaScenarioV2MetaData';
const assessmentIds = CHECKLIST_META_DATA
                        .filter(item => item.type === 'assessment');
const idList = INTV_META_DATA
                .filter(intv => intv.type === 'intervention')
                .sort((one, two) => {
                    if(one.label.localeCompare(two.label) < 0) return -1;
                    if(one.label.localeCompare(two.label) > 0) return 1;
                    return 0;
                });

export const matchesLabel = (str) => {
    if (!str || str === "") return null;
    let matchArr = [];

    for (let item of idList) 
        if (item.label?.toLowerCase().includes(str?.toLowerCase()))
            matchArr.push(item);

    return matchArr.length > 0 ? matchArr : null;
}

export const matchesId = (str) => {
    if (!str || str === "") return null;
    let matchArr = [];

    for (let item of idList) 
        if (item.id?.toLowerCase().includes(str?.toLowerCase()))
            matchArr.push(item);

    return matchArr.length > 0 ? matchArr : null;
}

export const matchesAny = (str) => {
    if (!str || str === "") return null;
    let matchArr = [];

    for (let item of idList) 
        if (item.label?.toLowerCase().includes(str?.toLowerCase()) || item.id?.toLowerCase().includes(str?.toLowerCase()))
            matchArr.push(item);

    return matchArr.length > 0 ? matchArr : null;
}

export const matchesChecklist = (str) => {
    if (!str || str === "") return null;
    let matchArr = [];

    for (let item of assessmentIds) 
        if (item.id?.toLowerCase().includes(str?.toLowerCase()))
            matchArr.push(item);

    return matchArr.length > 0 ? matchArr : null;
}