import { useState, useEffect, useRef } from 'react';
import { useHistory } from 'react-router';
import { getDocument, publishDocument } from '../../scripts/connect-firebase.js';
import { INTV_META_DATA } from '@meddbriefer/scenario-data/traumaScenarioV2MetaData';
import Header from '../Header';

const COLLECTION_ID = 'scenario-required-actions';

const ActionAssociations = (props) => {
    const { formId } = props.match.params;
    const [mdbForm, setMDBForm] = useState(null);
    const [prompts, setPrompts] = useState();
    const associationsRef = useRef();
    let history = useHistory();

    // Load in form data
    useEffect(() => {
        if (formId) {
            // Ensure the data has been uploaded to Firebase
            setTimeout(() => {
                getDocument(COLLECTION_ID, formId)
                    .then(res => setMDBForm(res))
                    .catch(err => console.error(err))
            }, 1000);            
        } else throw Error ('Unable to fetch debriefer info');
    }, [formId]);

    // Set list of prompts
    useEffect(() => {
        mdbForm &&
        setPrompts( getMatchingPrompts(mdbForm.criticalActions) );
    }, [mdbForm]);

    const handleSubmit = () => {
        let obj = {};
        for (let item of associationsRef.current)
        {
            if (item.tagName === 'SELECT')
            {
                obj[item.name] = item.value;
            }
            else
            {
                if (!obj[item.name]) obj[item.name] = [];
                if (item.checked)
                    obj[item.name].push(item.value);
            }
        }
        
        mdbForm.associations = obj;
        
        publishDocument(COLLECTION_ID, mdbForm.scenarioName, mdbForm);
        history.push('/debriefer-entry-list/');
        alert('Prompt assocations uploaded.');
    }

    const getSelected = (parent) => {
        return mdbForm.associations 
            ? mdbForm.associations[parent]
            : null;
    }
    const isChecked = (parent, item) => {
        return mdbForm.associations 
            ? mdbForm.associations[parent]?.includes(item) 
            : false;
    }

    return (
        <>
        <Header />
        <div id="associations-wrapper">
            <h1 id="title">Debriefer Prompt Associations</h1>
            <p className="hint-text">To be used to associate the correct action(s) with their interventions.</p>
            {
                mdbForm
                ? <form id="associations-list" ref={associationsRef} >
                    {prompts && prompts.map(intv => (
                        intv.prompts.map(prompt => (
                            prompt.selectionType === "one" 
                            ? // OR
                            <label key={prompt.id} className="associations-or">
                                {prompt.label}
                                {
                                <select name={prompt.id} defaultValue={getSelected(prompt.id)}>
                                    {
                                    prompt.children.map(child => (
                                        <option key={child.id} value={child.id}>{child.label}</option>
                                    ))
                                    }
                                </select>
                                }
                            </label>
                            : // AND
                            <div key={prompt.id} className="associations-and">
                                {prompt.label}
                                {
                                prompt.children.map(child => (
                                    <label key={child.id}>
                                        {child.label}
                                        <input type="checkbox" value={child.id} name={prompt.id} defaultChecked={isChecked(prompt.id, child.id)}/>
                                    </label>
                                ))
                                }
                            </div>
                            ))
                    ))}
                </form>
                : <p>Loading...</p>
            }
            {
                prompts && prompts.length === 0 &&
                <p>There is nothing to associate.</p>
            }
            <button type="button" onClick={handleSubmit} id="associations-submit">Submit</button>
        </div>
        </>
    );
}

const getAllInterventions = () => {
    let obj = {};

    for(let item of INTV_META_DATA)
    {
        if (item.type === 'intervention-form')
        {
            item.prompts = [];
            obj[item.id] = item;
        }
        else if (item.type === 'prompt')
        {
            if (item.form) {
                obj[ item.form ].prompts.push(item);
            }
        }
        else if (item.type === 'answer' || item.type === 'intervention')
        {
            if (item.prompt) {
                for (let intv in obj) {
                    for (let prompt of obj[intv].prompts) {
                        if (prompt.id === item.prompt) {
                            if (!prompt.children) prompt.children = [];
                            !prompt.children.includes(item) && prompt.children.push(item);
                        }
                    }
                }
            }
        }
    }

    return obj;
}

const getMatchingPrompts = (list) => {
    if (!list) return [];

    const allInterventions = getAllInterventions();
    let flattenedIds = [];
    let matchingPrompts = [];
    
    // Flatten Ids
    for(const block in list) {
        for (const item in list[block]){
            let curr = list[block][item];
            if (Array.isArray(curr) && item !== 'contraindicated') {
                flattenedIds.push(...curr);
            }
        }
    }
    
    // Get matching intervention prompts
    for (let id of flattenedIds) {
        for (let item of INTV_META_DATA) {
            if (id === item.id && item.relatedForm) {
                matchingPrompts.push(allInterventions[item.relatedForm]);
            }
        }
    }

    return matchingPrompts;
}

export default ActionAssociations;